export default {
  name: 'OIDCItem',
  props: {
    client: {
      type: Object,
      required: true
    }
  },
  data: function data() {
    return {
      id: this.client.id,
      name: this.client.name,
      redirectUris: this.client.redirectUris,
      clientId: this.client.clientId,
      clientSecret: this.client.clientSecret,
      signingAlg: this.client.signingAlg,
      type: this.client.type,
      renderSecret: false,
      addRedirectUri: ''
    };
  },
  computed: {
    renderedSecret: function renderedSecret() {
      if (this.renderSecret) {
        return this.clientSecret;
      } else {
        return '****';
      }
    },
    isPublic: function isPublic() {
      if (this.type === 'public') return true;
      return false;
    }
  },
  methods: {
    toggleSecret: function toggleSecret() {
      this.renderSecret = !this.renderSecret;
    },
    addRedirect: function addRedirect() {
      this.$emit('addredirect', this.id, this.addRedirectUri);
      this.addRedirectUri = '';
    }
  }
};